body {
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
  margin: 0;
  padding-bottom: 40px;
}

.printBTN {
  text-decoration: none;
  color: #000;
  padding: 10px 20px;
  background-color: #eee;
  border-radius: 3px;
}

#root div div:nth-child(2) {
  max-width: 1635px;
  padding: 40px;
  margin: 0 auto;
}

.row {
  clear: both; 
  margin: 0 auto 5px 0;
  text-align: center;
  width: 1635px;
  position: relative;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.rowName{
  display: block;
  position: absolute;
  top: 3px;
  left: -20px;
}

.rowName:nth-child(3) {
  right: -20px;
  left: auto;
}

.row ul {
  position: relative;
  width: 100%;
}

.zoneSelector {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  background: #f7f7f7;
  z-index: 1;
  padding: 20px;
}

.seat, .zoneSelector li {
  padding: 5px;
  margin: 0 1px 10px 0;
  position: relative;
  background: #d0d0ce;
  font-size: .7em;
  color:#000;
  width: 12px;
  height: 12px;
  text-align: center;
  cursor: pointer;
  user-select: none;
  border-radius: 100%;
  display: inline-block;
}


.zoneSelector li {
  font-size: 1em;
  border-radius: 3px;
  padding: 10px;
  width: 15px;
  height: 15px;
  margin: 0 0 0 10px;
  float: left;
}

.zoneSelector .zoneOne {
  background: #d0d0ce;
  color:#000;
}

.zoneSelector .zoneTwo {
  background: #0084d4;
  color: #fff;
}

.zoneSelector .zoneThree {
  background: #ef426f;
  color: #fff;
}

.zoneSelector .zoneFour {
  background: #ffdd46;
  color: #fff;
}

.zoneSelector .zoneFive {
  background: #26d07c;
  color: #fff;
}

.zoneSelector .zoneSix {
  background: #000;
  color: #fff;
}

.Z {
  margin-bottom: 60px;
}

.D ul :nth-child(1) {
  margin-left: 230px;
}

.J ul :nth-child(1) {
  margin-left: 280px;
}

.C38, .I43 {
  margin-right: 100px;
}

.X30, .X44, .Y46 {
  margin-right: 50px;
}

.Y15 {
  margin-right: 120px;
}

.Y39, .Z38 {
  margin-right: 375px;
}

.Z ul :nth-child(1) {
  margin-left: 115px;
}

